
/* eslint-disable */
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


import percent from './common/percent.vue'
import college from './boss/college'

export default {
components: {
Grid, GridItem,
GroupTitle,
Divider,
percent, Flexbox, FlexboxItem,
college
},
data() {
return {
access: 2,
permission: [{
gridList: [{
label: "到课率详表",
icon: "icon-renqun",
color: "#248888"
}, {
label: "缺课学生表",
icon: "icon-bug",
color: "#E7475E"
}]
}, {
gridList: [{
label: "辅导员分析",
icon: "icon-gerenxinxiyebanzhurenlaoshi",
color: "#248888"
}, {
label: "教师分析",
icon: "icon-custom-teacher",
color: "#E7475E"
}, {
label: "课程分析",
icon: "icon-xinrenkecheng",
color: "#248888"
}]
}, {
gridList: [{
label: "系部分析",
icon: "icon-huijihesuan",
color: "#248888"
}, {
label: "校园分析",
icon: "icon-xueyuan",
color: "#E7475E"
}]
}]
}
},
computed: {
GetPremisson() {
let str = [];
if (this.access != 3) {
for (let i = 0; i <= this.access; i++) {
str.push(...this.permission[i].gridList)
}
}
return str;
}
}
}



